* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #f1f5f8;
}
.container {
  width: 100%;
}
.form-container {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  align-items: center;
  margin-top: 20px;
  padding-left: 30px;
}
.form-container h1 {
  margin: 10px;
  margin-right: 40px;
  text-transform: capitalize;
  font-size: 30px;
  font-weight: 600;
  color: rgb(49, 47, 47);
  letter-spacing: 2px;
}
.form-group {
  /* text-align: center; */
  width: 100%;
  align-items: center;
}
.form-group .default {
  padding: 7px;
  font-size: 20px;
  border-color: transparent;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  margin: 5px;
}
.error {
  padding: 7px;
  font-size: 20px;
  border-color: transparent;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  border: 2px solid rgb(206, 37, 37);
}
.btn {
  font-size: 20px;
  text-transform: capitalize;
  padding: 7px;
  font-weight: 600;
  border-color: transparent;
  background-color: rgb(54, 180, 180);
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  color: white;
  outline-color: inherit;
}

.color-cotainer {
  width: 100%;
  min-height: calc(100vh - 100px);
  display: grid;
  margin-top: 20px;
  grid-template-columns: repeat(auto-fit, minmax(223.33px, 1fr));
  grid-template-rows: repeat(auto-fit, minmax(130px, 1fr));
}
.one-color {
  padding: 20px;
}
.lightcolor {
  color: rgb(255, 242, 242);
}
.color-Selector {
  border-color: transparent;
}
input[type='color'] {
  -webkit-appearance: none;
  /* border: none; */
  /* width: 35px; */
  height: 35px;
  border-radius: 5px;
}

@media only screen and (max-width: 350px) {
  .form-container {
    display: block;
    text-align: center;
    margin: auto;
    margin-top: 20px;
    padding-left: 0;
  }
  .form-container h1 {
    margin: 10px;
    margin-right: 0;
    font-size: 25px;
    font-weight: 600;
    color: rgb(49, 47, 47);
    letter-spacing: 2px;
  }
  .btn {
    margin-top: 10px;
    border-radius: 5px;
  }
  .form-group .default {
    border-radius: 5px;
  }
}
